.fullBody{
    background:#2b271a;
    color:#FFF;
    width:100%;
    height:100vh;
    text-align:center;
    font-size:20px;
    font-family: 'Quicksand';
    padding: 5%;
    padding-left: 10%;
    padding-right: 10%;
    overflow-x: hidden;
    word-wrap: break-word;
}
.App{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.logo{
    width:200px;
    height:200px
}
.smallLogo{
    width:100px;
    height:100px
}
.walletInfo{
    width:100%;
    word-wrap: break-word;
    border:1px dotted #FFF;
    border-radius: 10px;
    padding: 5%;
}

.block{
    border:1px solid #d9ff01;
    margin:2%;
    border-radius: 10px;
    padding: 5%;
    padding-top: 2%;
    padding-bottom: 2%;
    box-shadow: 1px 2px 10px #FFF;
}
.btnRed{
    background-color: #eaff01;
    margin-top: 10px;
    color:#000000;
    padding:8px 15px;
    border-radius: 5px;
}

.transaction{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 5%;
    padding-right: 5%;
}

.myBtn{
    background-color: rgb(251, 236, 69);
    color:#000;
    padding: 5px 10px;
    border-radius: 5px;
    box-shadow: 1px 2px 10px rgb(251, 251, 69);
    border: 1.5px solid #FFF;
    margin-bottom: 10px;
    width: 100%;
}
.myBtn:hover{
     background-color: rgb(251, 255, 27);
}

.btnHolder{
    display: flex;
    /* flex-direction: column; */
    justify-content: space-around;
    width:100%;
    position:absolute;
    top: 0px;
    left:0%;
    padding:10px;
    background-image: linear-gradient(to bottom,  rgb(72, 73, 15), transparent);
}

button a, a:hover{
    color:#200101;
    text-decoration:none;
    cursor:pointer;
}

.ConductTransaction .TransactionPool{
    margin: 10%;
}

.inputField{
    background-color: transparent !important;
    color: #FFF !important;
    border: 1px solid #FFF;
    transition: box-shadow 0.3s ease-in-out, border 0.3s ease-in-out;
    padding:10px;
}
.inputField:focus{
    box-shadow: 1px 2px 10px rgb(251, 251, 69) !important;
    outline: none !important; 
}

.submit{
    background-color: #7a6a03;
    border:1px solid #FFF;
    margin-top: 10px;
    color:#FFF;
    padding:8px 15px;
    border-radius: 10px;   
}
.submit:hover{
    background-color: #c4bb05;
}